<template>
    <div class="page-wrapper">
            <div class="clients-video isSoon" v-if="clients.isSoon">
                <h2> Próximamente... </h2>
            </div>
             <div v-if="!clients.isSoon">
                <div class="video-list-container hide-on-med-and-down">
                    <img class="client-image" :src="clients.logoPath" alt="">
                    <ul class="video-list">
                        <li v-for="(index, keyItem ) in clients.list"  class="list"  >
                            <a href="#" @click.prevent="showVideo(keyItem)" class="btn-video-link" :data-video-link="keyItem"  v-html="index.name">  </a>
                        </li>
                    </ul>
                </div>
                <div class="embed-video-container hide-on-med-and-down">
                    <div class="embed-video video-1" :data-embed="keyItem" v-for="(index, keyItem ) in clients.list" >
                        <div v-show="keyItem == 0 ">
                            <iframe :src="index.video" frameborder="0" allow="autoplay; fullscreen" allowfullscreen v-if="index.video"></iframe>
                        </div>
                        <div class="row" v-show="index.imageList">
                            <div class="imageList-carousel">
                            <carousel :perPage="1"
                                :paginationColor="'#321b44'"
                                :paginationActiveColor="'#fff'"
                                :navigationEnabled="true"
                                :navigationNextLabel="'<img width=15 src=/images/left-arrow.png>'"
                                :navigationPrevLabel="'<img width=15 src=/images/right-arrow.png>'">
                                <slide v-for="i in index.imageList">
                                    <div class="image-slide" :style="{'background' : 'url('+i.path+')', backgroundSize: 'contain', backgroundRepeat : 'no-repeat',   backgroundPosition: 'center', width: '100%', maxWidth:'720px', margin: '0 auto' }"></div>
                                </slide>
                            </carousel>
                            </div>
                        </div>
                        <div class="row" v-show="index.videoList">
                            <div class="imageList-carousel">
                                <carousel :perPage="1"
                                :paginationColor="'#321b44'"
                                :paginationActiveColor="'#fff'"
                                :navigationEnabled="true"
                                :navigationNextLabel="'<img width=15 src=/images/left-arrow.png>'"
                                :navigationPrevLabel="'<img width=15 src=/images/right-arrow.png>'">
                                    <slide v-for="i in index.videoList" style="text-align: center;">
                                        <video  class="image-slide" muted="true" autoplay="false" controls style="max-width: 720px; display: inline-block; text-align: center;">
                                            <source :src="i.path" type="video/mp4">
                                        </video>
                                    </slide>
                                </carousel>
                            </div>
                        </div>
                      
                    </div>
                </div>
                <div class="show-on-medium-and-down hide-on-med-and-up clients-mobile">
                    <img class="client-image-mobile slide-top" :src="clients.logoPath"  width="150" alt="">
                    <div class="card-list-mobile">
                        <div class="card mobile"  v-for="index in clients.list"  >
                            <div class="card-image waves-effect waves-block waves-light">
                                <div class="embed-video-container-mobile">
                                    <div class="embed-video-mobile">
                                        <div style="padding: 100px 0px;"  v-if="index.video">
                                            <iframe :src="index.video" frameborder="0" allow="autoplay; fullscreen" allowfullscreen ></iframe>
                                        </div>
                                        <div v-if="index.imageList"></div>
                                        <div v-for="indexImage in index.imageList"   style="height:200px; padding:20px;">
                                            <div :style="{'background' : 'url('+indexImage.path+')', backgroundSize: 'contain', backgroundRepeat : 'no-repeat',   backgroundPosition: 'center', height:'100%', maxWidth:'720px', margin: '0 auto' }"/>
                                        </div>
                                        <div v-if="index.videoList">
                                            <div v-for="indexVideo in index.videoList"  style="padding:10px;">
                                                <video  muted="true" controls style="width:100%">
                                                    <source :src="indexVideo.path" type="video/mp4">
                                                </video>
                                            </div> 
                                        </div>
                                    </div>
                                </div>
                            </div>
                            <div class="card-content">
                                <span class="card-title activator grey-text text-darken-4"> {{ index.name }}</span>
                            </div>
                        </div>
                    </div>

                </div>
                  
             </div>
             <router-link class="clients-video-back" tag="a" :to="{name: 'home-trabajo', params : { section : 'trabajo' } }">
                    <img width="10" src=/images/right-arrow.png style=" position: absolute; left: -20px; top: 3px;"> Ir atrás
                </router-link>
        </div>
</template>
<script>
import $ from 'jquery'
import { Carousel, Slide } from 'vue-carousel';

export default {
    
    props : [ 'clients' ],
    data() { 
        return {
            current: 0,
            direction: 1,
            transitionName: "fade",
            show: true,
        }
    },
    components: {
        Carousel,
        Slide
    },
    methods: {
        slide(dir, arrLength) {
        this.direction = dir;
        dir === 1
            ? (this.transitionName = "slide-next")
            : (this.transitionName = "slide-prev");
        var len = arrLength;
        this.current = (this.current + dir % len + len) % len;
        },
        showVideo( index ){
            this.current = 0
            $(this).css({ 'font-weight': 900 })
            $('.btn-video-link[data-video-link!="'+index+'"]').css({ 'font-weight': 400 })
            $('.embed-video[data-embed="'+index+'"]').fadeIn()
            $('.embed-video[data-embed!="'+index+'"]').fadeOut()
        }
    },
    created(){
        
        setTimeout( function(){
            $('.embed-video[data-embed!="0"]').fadeOut()
            let elements = document.querySelectorAll('.video-list-container');
            for (var i=0; i<elements.length; i++) {
                elements[i].classList.add('reveal');
            }
        },1000)
    },
}
</script>
