<template>
    <div class="sidebar-container">
         <ul id="sidenav" class="sidenav">
            <li>
                <router-link tag="a" class="logo-link" :to="{name: 'home'}" @click.native="removeModal">
                    <img class="image-logo" src="/images/Logos_Montalvo.png" alt="Montalvo">
                </router-link>
            </li>
            <li>
                <router-link tag="a" class="side-nav-clients" :to="{name: 'home-trabajo', params : { section : 'trabajo' } }" @click.native="removeModal">
                    Trabajo
                </router-link>
            </li>
            <li>
                <router-link tag="a" :to="{name: 'home-trabajo', params : { section : 'clientes' } }" @click.native="removeModal">
                    Clientes
                </router-link>
            </li>
             <li>
                <a href="mailto:talento@montalvokd.com">
                    Únete a nuestro equipo
               </a>
            </li>
            <li>
                <router-link tag="a" :to="{name: 'terms'}" @click.native="removeModal">
                    Términos y Condiciones
                </router-link>
            </li>
             <li>
                <router-link tag="a"  :to="{name: 'aviso'}" @click.native="removeModal">
                    Aviso de Privacidad
                </router-link>
            </li>
        </ul>
        <div id="burger-menu">
            <a href="#" data-target="sidenav" class="sidenav-trigger">
                <i id="burger-menu" class="material-icons" :class="{'black-text' : darkMode }">menu</i>
            </a>
        </div>
    </div>
</template>
<script>
import $ from 'jquery';
import M from  'materialize-css/dist/js/materialize.min.js'
import { setTimeout } from 'timers';
export default {
    props : [ 'darkMode' ],
  
    methods:{
        removeModal(  ){
           this.$router.beforeEach((to, from, next) => {
                next();
                setTimeout( function(){
                    if (  document.querySelector('.sidenav-overlay') ){
                        document.querySelectorAll('.sidenav-overlay').forEach( function(k ) {  k.remove() } )
                        document.querySelector('body').style.overflow = "unset"
                    }
                },1000)
 
           })
        }
    },
    created(){    
        setTimeout( function(){
            var elem = document.querySelector("#sidenav");
            M.Sidenav.init(elem, {
                edge: "left",
                inDuration: 250
            });
        }, 1000)
        var self = this
        $( function(){
            $(document).click( function(e){
               if( event.target.classList.value == 'sidenav-overlay' ){
                    if( document.querySelector( '.sidenav-trigger' ).classList.contains('dark') ) {
                        document.querySelector('.sidenav-trigger').classList.remove('dark')
                    } else {
                        document.querySelector('.sidenav-trigger').classList.add('dark')
                    }
               }
            })
            $('.sidenav-trigger').click( function(e){
                var instance =  M.Sidenav.getInstance( $("#sidenav") )
                    document.querySelector('.sidenav-trigger').classList.add('dark')
                if ( instance === undefined ){
                    var elem = document.querySelector("#sidenav");
                     M.Sidenav.init(elem, {
                        edge: "left",
                        inDuration: 250
                    });
                }
                console.log( instance )
                if (instance.isOpen){
                    instance.close()
                    instance.destroy()
                    document.querySelector('.sidenav-trigger').classList.remove('dark')
                }
                console.log( (instance.isOpen ) )
            })

            if( self.$route.params.section == 'trabajo' ){
                    $('html, body').animate({
                        scrollTop: $('#clients').offset().top
                    }, 1000);
                    document.querySelector('body').style.overflow = "unset"
                if( document.querySelector( '.sidenav-overlay' ) ){
                    document.querySelector('.sidenav-overlay').style.opacity = 1
                    document.querySelector('.sidenav-overlay').style.display = 'none'
                }

            }
            if( self.$route.params.section == 'clientes' ){
                    $('html, body').animate({
                        scrollTop: $('#clientes').offset().top
                    }, 1000);
                    document.querySelector('body').style.overflow = "unset"
                    if( document.querySelector( '.sidenav-overlay' ) ){
                        document.querySelector('.sidenav-overlay').style.opacity = 1
                        document.querySelector('.sidenav-overlay').style.display = 'none'
                    }
                    
                }
             
        })
    },
}
</script>

